* {
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
}

.main_div {
    width: 100%;
    height: 100vh;
    background: #6983aa;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.center_div {
    width: 25%;
    height: 60vh;
    background-color: #f4f4f4;
    box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
    border-radius: 15px;
    margin-top: 50px;
    /* display: flex; */
    /* flex-direction: column; */
    /* justify-content: center; */
    /* align-items: center; */
}

.btn_green:hover {
    color: #2ed573;
    background-color: white;
    cursor: pointer;
}

.btn_red:hover {
    color: #ff4757;
}

h1 {
    color: white;
    background: transparent;
    font-size: 50px;
    width: 100%;
    background-color: #8566aa;
    padding: 6px 0 2px 0;
    margin-bottom: 30px;
    box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
}

input {
    text-align: center;
    height: 30px;
    top: 10px;
    border: none;
    background: transparent;
    font-size: 20px;
    font-weight: 500;
    width: 60%;
    border-bottom: 2px solid #8566aa;
    outline: none;
}

button {
    min-height: 40px;
    width: 40px;
    border-radius: 50%;
    border-color: transparent;
    background-color: #8566aa;
    color: #fff;
    font-size: 20px;
    outline: none;
    border: none;
    margin-left: 30px;
    box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.3);
}

button:hover {
    background-color: gray;
}

.btn_div {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 100px;
}

ol {
    margin-top: 15px;
}

ol li {
    padding-left: 0px;
    text-align: center;
    font-size: 20px;
    font-weight: 500px;
    min-height: 40px;
    display: flex;
    align-items: center;
    color: #8566aa;
    text-transform: capitalize;
}

.todo_style {
    display: flex;
    flex-direction: row;
    /* justify-content: center; */
    align-items: center;
}

.todo_style .fa-times {
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #8566aa;
    border-radius: 50%;
    margin: 0 15px 0 35px;
    color: aliceblue;
    box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.3);
    cursor: pointer;
}

.todo_style .fa-times:hover {
    background-color: crimson;
}

@media (max-width:768px) {
    .center_div {
        width: 55%;
    }
    .todo_style .fa-times {
        margin: 0 15px 0 15px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    button {
        margin-left: 5px;
        min-height: 40px;
        width: 40px;
        color: #fff;
        font-size: 30px;
    }
}